//----------  forms
//------------------------------------------------------------------------------
input,
textarea,
select {
	height: 40px;
	border: 1px solid $gray;
	font-family: $font-default;
	padding: 0 10px;
	box-shadow:none;
}

/* The container */
.container {
	outline: 1px solid $gray;
	display: block;
	position: relative;
	padding-left: 35px;
	cursor: pointer;
	font-size: 22px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

/* Hide the browser's default checkbox */
.container input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

/* Create a custom checkbox */
.checkmark {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: $white;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
	background-color: $orange;
}

/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
	background-color: $orange;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
	content: "";
	position: absolute;
	display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
	display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
	margin: 0 auto;
	width: 15px;
	height: 30px;
	position:relative;
	border: solid white;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
}

.standard-form {
	text-align: center;
	margin: 0 auto;
	width: 100%;
	position: relative;

	&.md {
		width: 33%;

		@include media-breakpoint-down(sm) {
			width: 100%;
		}
	}

	input,
	textarea,
	select {
		border-radius: 5px;
		width: 100%;
	}

	textarea {
		height: 300px;
	}

	li {
		padding-top: 30px;
	}

	label {
		display: block;
		padding-bottom: 10px;
		text-transform: uppercase;
		font-weight: bold;
	}
}

// validation css
.field-validation-error {
	font-size: 11px;
	position: absolute;
	z-index: 10;
	margin-top: 6px;
	color: #ffffff;
	background: #ffaf46;
	box-shadow: 0 6px 2px -2px rgba(207, 137, 44, 0.47);
	padding: 5px 10px;
	border-radius: 6px;
	pointer-events: none;
	left: 0;
	line-height: 11px;
	transition: opacity 0.5s linear;
}

.table .field-validation-error {
	margin-top: 46px !important;
}

.field-validation-error:before {
	content: '';
	width: 0;
	height: 0;
	top: -5px;
	position: absolute;
	left: 6px;
	border-left: 5px solid rgba(0, 0, 0, 0);
	border-right: 5px solid rgba(0, 0, 0, 0);
	border-bottom: 5px solid #ffaf46;
}

.field-validation-valid {
	opacity: 0;
}

.input-validation-error {
	border: 1px solid #ffaf46;
	color: #ffaf46;
}

.valid {
	border: 1px solid #cccbcb;
}

.validation__wrapper {
	position: relative;
	-webkit-box-flex: 1;
	-webkit-flex-grow: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
}

.validation__wrapper input, select {
	width: 100%;
}
