//---------- filter
//------------------------------------------------------------------------------

.filter {
	background-color: $black;
	display: flex;
	color: $white;
	margin: 15px 0 20px;
	justify-content: stretch;
}
.filter__item {
	flex-grow: 1;
	display:flex;
	padding: 10px;
	line-height:40px;

	input, textarea, select{
		flex-grow:1;
	}
}

.filter__text{
	padding-right: 10px;
}

.table__wrapper {
	overflow-x: auto;
}

.board__filter-table {
	width: 100%;
	background-color: $black;

	thead,
	tfoot {
		color: $white;
		font-size: 17px;
		width: 100%;
	}
	tbody {
		background-color: $white;
		tr td {
			outline: 1px solid $gray;
			outline-offset: -0.5px;
		}
	}
	th, td {
		text-align: left;
		padding: 10px;
	}
	td b {
		color: $orange;
	}
	@include media-breakpoint-down(md) {
		white-space: nowrap;
	}
}