.accordion__content {
	display: flex;
	padding-top: 20px;
	padding-bottom: 20px;
}

.field {
	padding-bottom: 20px;
	display: inline-flex;
	width:100%;
	line-height: 40px;

	input, select{
		flex-grow:1;
	}

	label{
		padding-right:20px;
	}
}

.accordion__buttons {
	button:first-child {
		margin-right: 10px;
	}

	button::before {
		padding: 5px 20px 5px 20px;
	}
}
