  background: -webkit-linear-gradient(top left, #444444 50%, #ffaf46 50%);
  background: -moz-linear-gradient(top left, #444444 50%, #ffaf46 50%);
  background: -ms-linear-gradient(top left, #444444 50%, #ffaf46 50%);
  background: -o-linear-gradient(top left, #444444 50%, #ffaf46 50%);
  background: linear-gradient(top left, #444444 50%, #ffaf46 50%);
}

.popup:before {
  content: '';
  position: absolute;
  left: 5%;
  top: 5%;
  width: 90%;
  height: 90%;
  border-radius: inherit;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 40px 20px rgba(0, 0, 0, 0.1);
  transform: translateZ(-100px);
}

.popup-content {
  background: #fff;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 10px 0 10px 0;
}

.popup-text {
  text-align: center;
  transform: translateZ(15px);
}

.popup-text b {
  color: #ffaf46;
  font-weight: 300;
}

.background__canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
}

.error__page {
  background: #272727;
}

.login {
  position: relative;
  height: 100%;
}

.login__box {
  max-width: 400px;
  width: 80%;
  background-color: #fff;
  border-radius: 30px;
  border: 1px solid #cccbcb;
  text-align: center;
  padding: 50px 50px 45px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 767.98px) {
  .login__box {
    border: unset;
    padding: 0px 10px 0px 10px;
    width: 100%;
  }
}

.login__logo {
  margin-bottom: 50px;
}

.login__btn {
  margin-bottom: 22px;
}

.login__link {
  display: block;
  font-weight: 700;
}

.login__item {
  margin-bottom: 24px;
}

.login__item input {
  height: 40px;
  line-height: 40px;
  border: 1px solid #cccbcb;
  border-radius: 5px;
  width: 100%;
  color: #444;
  font-weight: 600;
}

.login__item input::placeholder {
  color: #444;
}

.accordion__content {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
}

.field {
  padding-bottom: 20px;
  display: inline-flex;
  width: 100%;
  line-height: 40px;
}

.field input, .field select {
  flex-grow: 1;
}

.field label {
  padding-right: 20px;
}

.accordion__buttons button:first-child {
  margin-right: 10px;
}

.accordion__buttons button::before {
  padding: 5px 20px 5px 20px;
}

.filter {
  background-color: #444444;
  display: flex;
  color: #fff;
  margin: 15px 0 20px;
  justify-content: stretch;
}

.filter__item {
  flex-grow: 1;
  display: flex;
  padding: 10px;
  line-height: 40px;
}

.filter__item input, .filter__item textarea, .filter__item select {
  flex-grow: 1;
}

.filter__text {
  padding-right: 10px;
}

.table__wrapper {
  overflow-x: auto;
}

.board__filter-table {
  width: 100%;
  background-color: #444444;
}

.board__filter-table thead,
.board__filter-table tfoot {
  color: #fff;
  font-size: 17px;
  width: 100%;
}

.board__filter-table tbody {
  background-color: #fff;
}

.board__filter-table tbody tr td {
  outline: 1px solid #cccbcb;
  outline-offset: -0.5px;
}

.board__filter-table th, .board__filter-table td {
  text-align: left;
  padding: 10px;
}

.board__filter-table td b {
  color: #ffaf46;
}

@media only screen and (max-width: 991.98px) {
  .board__filter-table {
    white-space: nowrap;
  }
}
