//---------- users
//------------------------------------------------------------------------------

.letters {
	display: flex;
	padding-top: 30px;
	flex-wrap: wrap;

	.letters__search {
		flex: 3;
		max-width: unset;
		display: flex;
		flex-wrap: wrap;

		input {
			min-width: 10px;
			margin-top: 1px;
			flex: 1;
			border: none;
		}

		button {
			padding: 5px;
			color: $gray;
		}
	}
}

.pagination {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	margin: 0.5px;
}

.box__link {
	flex: 1;
	outline: 1px solid $gray;
	outline-offset: -0.5px;
	min-width: 38.5px;
	max-width: 45px;
	min-height: 37.5px;
	text-align: center;
	line-height: 37.5px;
	flex-wrap: wrap;
	font-weight: bold;

	a {
		width: 100%;
		height: 100%;
		display: block;
	}
}
